<template>
    <section class="page">
      <div class="content">
        <h2>About Me</h2>
  
        <div class="page-introduction">
          <h3>Who Am I?</h3>
          <p>
            Tsup! I’m Jeremy. My goal? To leave a trace of elegance in a world that’s constantly rushing toward the next big thing.
            I think life is far too precious to spend doing things you don’t love, just for things you don’t need, to impress people you don’t know.
            Call me idealistic, but I’d rather be broke and inspired than rich and bored.
            (Though I wouldn’t mind being inspired and comfortable, let’s be honest.)
          </p>
          <p>Explore more about me. I am sure that you gonna like me. (At least I tried.)</p>
        </div>
  
        <div class="card">
          <h3>My Role</h3>
          <p>I am large, I contain multitudes.</p>
          <router-link :to="'/about/role/'" class="card-btn">Explore More</router-link>
        </div>
  
        <div class="card">
          <h3>My Timeline</h3>
          <p>Since 2002.3.22 6:23 AM, I have already lived {{ timeElapsed }}.</p>
          <router-link :to="'/about/role/'" class="card-btn">Explore More</router-link>
        </div> 
  
        <!-- 敬请期待的文字 -->
        <p class="wait-text">Please wait for more...</p>
      </div>
    </section>
  </template>
  
  <script>
  export default {
    name: 'AboutPage',
    data() {
      return {
        timeElapsed: '',
        timer: null,
      };
    },
    methods: {
      updateElapsedTime() {
        const startTime = new Date('2002-03-22T06:23:00');
        const now = new Date();
        const diff = now - startTime;
  
        const seconds = Math.floor(diff / 1000);
        const days = Math.floor(seconds / (60 * 60 * 24));
        const hours = Math.floor((seconds % (60 * 60 * 24)) / (60 * 60));
        const minutes = Math.floor((seconds % (60 * 60)) / 60);
        const secs = seconds % 60;
  
        this.timeElapsed = `${days} days, ${hours} hours, ${minutes} minutes, ${secs} seconds`;
      },
    },
    mounted() {
      this.updateElapsedTime();
      this.timer = setInterval(this.updateElapsedTime, 1000); // 每秒更新
    },
    beforeUnmount() {
      clearInterval(this.timer);
    },
  };
  </script>

