<template>
    <section class="skills">
      <div class="content">
        <h2>My Skills</h2>
  
        <!-- 筛选按钮 -->
        <div class="filter">
          <button v-for="filter in filters" 
                  :key="filter.value" 
                  :class="['filter-btn', { active: selectedFilter === filter.value }]"
                  @click="filterSkills(filter.value)">
            {{ filter.label }}
          </button>
        </div>
  
        <!-- 技能列表 - 网格展示 -->
        <div class="skills-grid">
          <div v-for="skill in filteredSkills" :key="skill.id" class="skill-card">
            <h3>{{ skill.name }}</h3>
            <p>{{ skill.description }}</p>
            <div class="skill-level">
              <div class="level-bar" :style="{ width: skill.level + '%' }"></div>
            </div>
          </div>
        </div>
  
      </div>
    </section>
  </template>
  
  <script>
  export default {
    data() {
      return {
        skills: [
          { id: 1, name: 'Vue.js', description: 'A JavaScript framework for building user interfaces.', level: 90, category: 'Programming Language' },
          { id: 2, name: 'JavaScript', description: 'A programming language used to create dynamic and interactive web applications.', level: 85, category: 'Programming Language' },
          { id: 3, name: 'CSS', description: 'A stylesheet language used to describe the presentation of a document written in HTML.', level: 80, category: 'Programming Language' },
          { id: 4, name: 'Node.js', description: 'A runtime environment that allows JavaScript to be run on the server side.', level: 70, category: 'Programming Language' },
          { id: 5, name: 'Python', description: 'A high-level programming language for general-purpose programming.', level: 75, category: 'Programming Language' },
          { id: 6, name: 'C', description: 'A general-purpose programming language widely used for system programming and embedded systems.', level: 80, category: 'Programming Language' },
          { id: 7, name: 'C++', description: 'An extension of C with object-oriented programming capabilities, used for performance-critical applications.', level: 75, category: 'Programming Language' },
          { id: 8, name: 'Matlab', description: 'A high-level language and environment for numerical computing, data analysis, and algorithm development.', level: 70, category: 'Programming Language' },
          { id: 9, name: 'Microsoft Office', description: 'A suite of productivity software, including Word, Excel, PowerPoint, and Outlook.', level: 90, category: 'Other' },
          { id: 10, name: 'English', description: 'Fluent in English, both written and spoken.', level: 95, category: 'Language' },
          { id: 11, name: 'French', description: 'Fluent in French, both written and spoken.', level: 85, category: 'Language' },
          { id: 12, name: 'German', description: 'Intermediate proficiency in German, both written and spoken.', level: 70, category: 'Language' },
        ],
        selectedFilter: 'all',  // 默认筛选所有技能
        filteredSkills: [],  // 默认展示所有技能
        filters: [
          { label: 'All', value: 'all' },
          { label: 'Programming Language', value: 'Programming Language' },
          { label: 'Language', value: 'Language' },
          { label: 'Other', value: 'Other' }
        ]
      };
    },
    methods: {
      filterSkills(filterValue) {
        this.selectedFilter = filterValue;
        
        if (filterValue === 'Programming Language') {
          // 筛选编程语言
          this.filteredSkills = this.skills.filter(skill => skill.category === 'Programming Language');
        } else if (filterValue === 'Language') {
          // 筛选语言
          this.filteredSkills = this.skills.filter(skill => skill.category === 'Language');
        } else if (filterValue === 'Other') {
          // 筛选其他技能
          this.filteredSkills = this.skills.filter(skill => skill.category === 'Other');
        } else {
          // 如果选择了所有技能，显示所有技能
          this.filteredSkills = this.skills;
        }
      }
    },
    created() {
      this.filteredSkills = this.skills;  // 初始化时显示所有技能
    }
  };
  </script>
  
