import { createRouter, createWebHistory } from 'vue-router';

// 导入页面组件
import Home from '../pages/HomePage.vue';


import About from '../pages/about/AboutPage.vue';
import Timeline from '@/pages/about/timeline/TimelinePage.vue';

import Testimonials from '@/pages/about/testimonial/TestimonialsPage.vue';
import TestimonialDetail from '@/pages/about/testimonial/TestimonialDetail.vue';

import Skills from '../pages/about/skills/SkillsPage.vue';


import Projects from '../pages/projects/ProjectsPage.vue';


import Blog from '../pages/blogs/BlogPage.vue';


import Contact from '../pages/ContactPage.vue';


import NotFound from '../pages/404NotFoundPage.vue';

// 定义路由配置
const routes = [
  
  {
    path: '/',
    name: 'home',
    component: Home,
  },
  {
    path: '/about',
    name: 'about',
    component: About,
  },
  {
    path: '/about/timeline',
    name: 'timeline',
    component: Timeline,
  },
  {
    path: '/about/testimonial',
    name: 'testimonials',
    component: Testimonials,
  },
  {
    // 动态路由，捕获 testimonial 的 id
    path: '/about/testimonial/:id',
    name: 'testimonialDetail',
    component: TestimonialDetail,
    props: true,  // 使路由参数作为 prop 传递给组件
  },
  {
    path: '/about/skills',
    name: 'skills',
    component: Skills,
  },

  {
    path: '/projects',
    name: 'projects',
    component: Projects,
  },
  {
    path: '/blogs/blog',
    name: 'blog',
    component: Blog,
  },
  {
    path: '/contact',
    name: 'contact',
    component: Contact,
  },
  {
    path: '/:catchAll(.*)',
    name: 'not-found',
    component: NotFound,
  },
];

// const router = createRouter({
//   history: createWebHistory(),
//   routes,
// });

// 创建路由实例，传入 base 配置
const router = createRouter({
  history: createWebHistory('/portfolio/'),  // 这里是你的 base 路径
  routes,
  scrollBehavior(to, from, savedPosition) {
    // 如果有保存的位置（比如使用浏览器“返回”按钮），就返回那个位置
    if (savedPosition) {
      return savedPosition;
    } else {
      // 否则总是滚动到页面顶部
      return { top: 0 };
    }
  },
});


export default router;